@keyframes FadeIn {
    to {
        opacity: 1;
    }
}

@keyframes DropInFromTop {
    60% {
        transform: translateY(20%);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes PopInFromBottom {
    60% {
        transform: translateY(-10%);
        opacity: 1;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes PopInScale {
    60% {
        transform: scale(110%);
    }
    100% {
        transform: scale(100%);
    }
}

@keyframes RotateVertical {
    70% {
        transform: rotateY(-400deg);
    }

    100%{
        transform: rotateY(-360deg);
    }
}